import React, { Component } from 'react'
import * as emailjs from 'emailjs-com'
import { Button, Form, FormGroup, Label, Input } from 'reactstrap'

import '../Styles/App.css'

import Contactimg from '../images/Contact1.svg'

import ContactMailIcon from '@material-ui/icons/ContactMail';
import PermContactCalendarIcon from '@material-ui/icons/PermContactCalendar';
import SubjectIcon from '@material-ui/icons/Subject';
import MessageIcon from '@material-ui/icons/Message';

function handleClick(e) {
  e.preventDefault();
  alert('Thank you for your submission.');
}

class Contact extends Component {
  state = {
    name: '',
    email: '',
    subject: '',
    message: '',
  }
handleSubmit(e) {
    e.preventDefault()
    const { name, email, subject, message } = this.state
    let templateParams = {
      from_name: email,
      to_name: 'mdragunservice@gmail.com',
      subject: subject,
      message_html: message,
     }
     emailjs.send(
      'testg',
      'template_dvu6qkEa',
       templateParams,
      'user_kEZ7zu5x7qUKecP2a3u3g'
     )
     this.resetForm()
 }
resetForm() {
    this.setState({
      name: '',
      email: '',
      subject: '',
      message: '',
    })
  }
handleChange = (param, e) => {
    this.setState({ [param]: e.target.value })
  }

 

render() {
    return (
      <div className="contact-container">
     
      <div className='form-container' >
        <div className="contact-header">
          <img src={Contactimg}></img>
        </div>
          <Form className='form-main'  onSubmit={this.handleSubmit.bind(this)} >
            <FormGroup controlId="formBasicEmail">
              <Label > <ContactMailIcon className='form-icon' style={{marginRight:'10px'}}  />Email address</Label>
              <Input
              required
                type="email"
                name="email"
                value={this.state.email}
                className="text-primary"
                onChange={this.handleChange.bind(this, 'email')}
                placeholder="Enter email"
              />
            </FormGroup>
<FormGroup controlId="formBasicName">
              <Label  > <PermContactCalendarIcon style={{marginRight:'5px'}} /> Name</Label>
              <Input
              required
                type="text"
                name="name"
                value={this.state.name}
                className="text-primary"
                onChange={this.handleChange.bind(this, 'name')}
                placeholder="Name"
              />
            </FormGroup>
<FormGroup controlId="formBasicSubject">
              <Label > <SubjectIcon /> Subject</Label>
              <Input
              required
                type="text"
                name="subject"
                className="text-primary"
                value={this.state.subject}
                onChange={this.handleChange.bind(this, 'subject')}
                placeholder="Subject"
              />
            </FormGroup>
<FormGroup controlId="formBasicMessage">
              <Label > <MessageIcon /> Message</Label>
              <Input
              required
                type="textarea"
                name="message"
                className="text-primary"
                value={this.state.message}
                onChange={this.handleChange.bind(this, 'message')}
              />
            </FormGroup>
<Button color="success" type="submit" onClick={handleClick} style={{padding:'10px 30px'}} >
              Submit
            </Button>
    </Form>
      </div>
      </div>
    )
  }
}
export default Contact